
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import CountDown from './count-down.vue'
import Data from "./data.js";
import Scroll from "./scroll.vue";

const url = 'https://wx.sharkmeida.cn/distribution/info/95ce4038a19b49f5bfedb8722eef9ecf'
export default {
name: "info",
components: {
Group,
Cell,
Loading,
Datetime,
XInput,
XNumber,
XSwitch,
XTextarea,
Scroll,
CountDown
},
data() {
return {
list: [{
img: 'https://wx.sharkmeida.cn/imgs/82ef949d-3af8-466a-ad94-4edc1d215df5.jpg',
text: '第2条公sdsjdksjkdjksjkajkjdkhsajdhkajdhjshajhjdjqwjhjqhjhwjqhbnbbbsah告'
}],
formD: Data,
isLoading: true,
musicUrl:""
};
},
props: {
form: {
type: Object
}
},
mounted(){
this.query();
},
methods:{
stop(){
var audio = document.getElementById("audio2");
if(audio == null)
return
audio.pause();
},
play(item){
var audio = document.getElementById("audio2");
if(audio == null)
return
audio.pause();
audio.load();
audio.play();
},
query(){
if(!!this.form){
this.formD = this.form;
return
}
this.$http.get(url).then(({data}) => {
if(data.code == '0000'){
this.formD = data.distribution
this.formD.music = JSON.parse(this.formD.music)
var thumbnail = data.distribution.thumbnail
var currentUrl =  'https://wx.sharkmeida.cn/dist/index.html'

this.$http.post('https://wx.sharkmeida.cn/api/wxpay/initwxjs?url='+currentUrl).then(function (data, status) {
wx.config({
debug: true, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
appId: data.data.result.data.appId, // 必填，公众号的唯一标识
timestamp: data.data.result.data.timestamp, // 必填，生成签名的时间戳
nonceStr: data.data.result.data.nonceStr, // 必填，生成签名的随机串
signature: data.data.result.data.signature,// 必填，签名
jsApiList: ['onMenuShareTimeline','onMenuShareAppMessage'] 
});

wx.ready(function(){


wx.error(function(res){

console.log(res);
});

wx.checkJsApi({
jsApiList: ['onMenuShareTimeline','onMenuShareAppMessage'], // 需要检测的JS接口列表，所有JS接口列表见附录2,
success: function(res) {


console.log('checkJsApi'+ res);
}
});

var shareParam = {
title: '分销', // 分享标题
desc: '这是一条描述', // 分享描述
link: currentUrl, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
imgUrl: thumbnail, // 分享图标


trigger: function (res) {
console.log('用户点击发送给朋友');
},
success: function (res) {
console.log('已分享');
},
cancel: function (res) {
console.log('已取消');
},
fail: function (res) {
}
};

wx.onMenuShareTimeline(shareParam);
wx.onMenuShareAppMessage(shareParam);

});

})
}
})
}
}
};



