export default {
  name: 'scroll',
  data () {
    return {
    }
  },
  props: {
      list: {
          type: Array,
          defaultData: [{
              img: '@/assets/back-top-3.png',
              text: '第一条公告'
          }]
      }
  },
  mounted(){
      this.init();
  },
  methods: {
      init(){
        var box = document.getElementById('scroll-box')	
        var parent = document.getElementsByTagName('ul')[0]	
        var parent2 = document.getElementsByTagName('ul')[1]	 
        parent2.innerHTML = parent.innerHTML      /*启动定时器*/     
        var timer = setInterval(autoScrollLine, 30)    /*单行向上滚动效果*/   
        function autoScrollLine() {     /*判断滚动内容是否已经滚完，滚完了则滚动的值重新设置到0     否则就每隔30毫秒向上滚动1px*/     
        if (box.scrollTop >= parent.offsetHeight) 
        { box.scrollTop = 0; } else { box.scrollTop++; }      /*判断滚动的距离刚好为一条公告的高度时停掉定时器，       隔1s之后重新启动定时器即可实现公告滚动停留效果 */      
        if (box.scrollTop % box.offsetHeight == 0) { 
            clearInterval(timer)     	
            setTimeout(() => { timer = setInterval(autoScrollLine, 30) }, 0) } }
        }
  }
}



