
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import AddImg from "./addImg.vue";
import ImgUpload from "./imgUpload.vue";
import Info from './info.vue';
import Data from "./data.js";
console.log(Data)
export default {
name: "content",
components: {
Group,
Cell,
Loading,
Datetime,
XInput,
XNumber,
XSwitch,
XTextarea,
ImgUpload,
Info,
AddImg
},
data() {
return {
formD: Data,
isLoading: true,
show: true,
musicUrl:"",
ispreview: false,
activeList:[],
musicList: [
{
label: '天空之城',
status: 0,
active: 0,
value: "http://www.ytmp3.cn/down/53430.mp3"
},
{
label: '天空之城2',
status: 0,
active: 0,
value: "http://www.ytmp3.cn/down/52790.mp3"
}
]
};
},
methods:{
editpage(){
this.ispreview = !this.ispreview;
this.$refs['info'].stop();
},
preview(){
console.log(this.formD);
var activityRules = [], activityDestription = [];
this.$refs['activityRules'].activeList.forEach(item => {
activityRules.push({
type: item.type,
img: item.image
})
})
this.$refs['activityDestription'].activeList.forEach(item => {
activityDestription.push({
type: item.type,
img: item.image
})
})
this.formD.activityRules = JSON.stringify(activityRules);
this.formD.activityDestription = JSON.stringify(activityDestription);
this.formD.music = JSON.stringify({
path:this.musicUrl
})
console.log(this.formD);
var a = JSON.parse(JSON.stringify(this.formD))
for(var key in a){
if(a[key] === true){
a[key] = 1
}
if(a[key] === false){
a[key] = 0
}
}

this.$refs['info'].play();
this.ispreview = !this.ispreview;
},
save(){
console.log(this.formD);
var activityRules = [], activityDestription = [];
this.$refs['activityRules'].activeList.forEach(item => {
activityRules.push({
type: item.type,
img: item.image
})
})
this.$refs['activityDestription'].activeList.forEach(item => {
activityDestription.push({
type: item.type,
img: item.image
})
})
this.formD.activityRules = JSON.stringify(activityRules);
this.formD.activityDestription = JSON.stringify(activityDestription);
this.formD.music = this.musicUrl;
console.log(this.formD);
var a = JSON.parse(JSON.stringify(this.formD))
for(var key in a){
if(a[key] === true){
a[key] = 1
}
if(a[key] === false){
a[key] = 0
}
}
this.$vux.loading.show({
text: 'Loading'
})
this.$http.post('https://wx.sharkmeida.cn/distribution/save', a).then(({data}) => {
this.$vux.loading.hide()
if(data.code == '0'){
this.$vux.alert.show({
title: '提示',
content: '保存成功！',
onShow () {
console.log('Plugin: I\'m showing')
},
onHide () {
console.log('Plugin: I\'m hiding')
}
})
}else{
this.$vux.alert.show({
title: '提示',
content: '保存失败！',
onShow () {
console.log('Plugin: I\'m showing')
},
onHide () {
console.log('Plugin: I\'m hiding')
}
})
}
})
},
cancel(){
this.stop();
this.show = !this.show
},
musicClick(){
this.show = !this.show
if(!this.show){
this.musicList.forEach(el => {
if(el.value == this.musicUrl){
el.active = 1
}else{
el.active = 0
}
})
}
},
play(item){
var audio = document.getElementById("audio");
audio.src = item.value;
audio.loop = true; 
this.musicList.forEach(el => {
el.status = 0;
})
item.status = 1;
audio.pause();
audio.load();
audio.play();
},
stop(){
var audio = document.getElementById("audio");
audio.pause();
audio.load();
this.musicList.forEach(el => {
el.status = 0;
})
},
tab(item){
this.musicList.forEach(el => {
el.active = 0;
})
item.active = 1
},
sure(){
this.stop();
var item
this.musicList.forEach(el => {
if(el.active == 1){
item = el;
};
})
if(!!item){
var audio = document.getElementById("audio");
audio.src = item.value;
this.$vux.toast.show({
type: "success",
text: '选择成功！',
position: 'middle'
})
this.musicUrl = item.value;
this.show = !this.show;
}else{
this.$vux.toast.show({
type: "warn",
text: '请选择一条',
position: 'middle'
})
}
}
}
};



